@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeInDown {
  0% {
    opacity: 0;
    transform: translateY(20px); }
  100% {
    opacity: 1;
    transform: translateY(0px); } }

@keyframes hexagonAnimation {
  0% {
    transform: rotate(0deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(45deg) scale(1);
    opacity: 1; } }

@keyframes centerVerticalLineAnimation {
  0% {
    transform: rotate(-45deg) scale(0.5);
    opacity: 0; }
  100% {
    transform: rotate(0deg) scale(1);
    opacity: 1; } }

@keyframes leftVerticalLineAnimation {
  0% {
    left: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    left: calc(50% - 23px);
    opacity: 1; } }

@keyframes letterALineAnimation {
  0% {
    width: 0; }
  100% {
    width: 20px; } }

@keyframes letterHLineAnimation {
  0% {
    width: 0; }
  100% {
    width: 14px; } }

@keyframes rightVerticalLineAnimation {
  0% {
    right: calc(50% - 3px);
    opacity: 0; }
  1% {
    opacity: 1; }
  100% {
    right: calc(50% - 23px);
    opacity: 1; } }

@keyframes arrowDrawIn {
  from {
    transform: translateY(-20px);
    stroke-dashoffset: 85; }
  to {
    transform: translateY(0px);
    stroke-dashoffset: 0; } }

.logo {
  width: 100px;
  height: 100px;
  margin-top: 40px;
  position: relative; }
  .logo .hexagon {
    stroke: white;
    stroke-width: 18px;
    fill: transparent;
    overflow: unset;
    position: absolute;
    opacity: 0;
    -webkit-animation: hexagonAnimation 1s 0.5s forwards;
    animation: hexagonAnimation 1s 0.5s forwards; }
  .logo .center-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    left: calc(50% - 3px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: centerVerticalLineAnimation 1s 0.5s forwards;
    animation: centerVerticalLineAnimation 1s 0.5s forwards; }
  .logo .left-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    left: calc(50% - 23px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: leftVerticalLineAnimation 0.5s 1.5s forwards;
    animation: leftVerticalLineAnimation 0.5s 1.5s forwards; }
  .logo .right-v-line {
    width: 6px;
    height: 40px;
    background: white;
    position: absolute;
    right: calc(50% - 23px);
    top: calc(50% - 20px);
    opacity: 0;
    -webkit-animation: rightVerticalLineAnimation 0.5s 1.75s forwards;
    animation: rightVerticalLineAnimation 0.5s 1.75s forwards; }
  .logo .a-top-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 20px);
    -webkit-animation: letterALineAnimation 0.8s 2s forwards;
    animation: letterALineAnimation 0.8s 2s forwards; }
  .logo .a-mid-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    left: calc(50% - 20px);
    top: calc(50% - 3px);
    -webkit-animation: letterALineAnimation 0.8s 2s forwards;
    animation: letterALineAnimation 0.8s 2s forwards; }
  .logo .h-mid-line {
    width: 0px;
    height: 6px;
    background: white;
    position: absolute;
    right: calc(50% - 20px);
    top: calc(50% - 3px);
    -webkit-animation: letterHLineAnimation 0.8s 2.25s forwards;
    animation: letterHLineAnimation 0.8s 2.25s forwards; }



